import React, { Component } from 'react';
import {CInput, CInputGroup, CTooltip} from '@coreui/react';
import CIcon from '@coreui/icons-react';
import Select from 'react-select';
import { Logger } from 'src/Utils/Utils';
import { unitOfMeasurementsList } from 'src/Utils/Enums';

//default
const FILE_NAME = "ItemInputForm.js";

//default
const initialState = {
    isLoaded: false,
    unitOfMeasurement: null
};

const propsItems = {
    itemIndex: 'item',
    onItemDeletion: 'function',
    onProductNameSelection: 'function',
    productListDataSelectTagSpecific: 'item'
};

class ItemInputForm extends Component { //dynamic input form for `item-data`.

    //default
    constructor(props) {
        super(props);
        this.state = JSON.parse(JSON.stringify(initialState));
    }

    //default
    componentDidMount() {
    }

    //default
    render() {
        //default
        Logger(FILE_NAME, "render", "", "this.state", this.state);
        Logger(FILE_NAME, "render", "", "this.props", this.props);

        return (
            <div style={{
                borderStyle:"solid",
                borderColor:"red",
                padding: "10px 10px 10px 10px",
                margin: "0px 0px 10px 0px"
            }}>
                <h5><CIcon size='lg' name="cilMoney" />&nbsp;&nbsp;Item #{this.props.itemIndex + 1} :
                <CTooltip content='üóëÔ∏è Delete this item'>
                    <span style={{float: 'right', borderStyle:'solid', borderColor:'black', padding: '3px'}} onClick={() => {
                        if(window.confirm("Surely want to DELETE item #" + (this.props.itemIndex + 1) + " ?"))
                            this.props.onItemDeletion(this.props.itemIndex);
                        }}>‚ùå üóëÔ∏è</span>
                </CTooltip>
                </h5>

                <Select 
                    onChange={(choice) => {
                        this.props.onProductNameSelection(choice, this.props.itemIndex);
                        this.setState({
                            unitOfMeasurement: (choice === null) ? null : choice.unitOfMeasurement
                        });
                    }}
                    options={this.props.productListDataSelectTagSpecific}
                    placeholder='Product name'
                    isClearable={true}
                />
                <CInputGroup className="mb-3">
                    <CInput
                        placeholder="Quantity" 
                        autoComplete="on"
                        type="number"
                        step="0.01"
                        id={"inputItemQuantity_" + this.props.itemIndex}
                        label="Quantity"
                        required
                    />&nbsp;&nbsp;
                    <b>
                        {(this.state.unitOfMeasurement === null) ? "~" 
                        : unitOfMeasurementsList.find(unitOfMeasurement => unitOfMeasurement.value === this.state.unitOfMeasurement).label 
                        }
                    </b> &nbsp;&nbsp;
                    <CInput
                        placeholder="Amount" 
                        autoComplete="on"
                        type="number"
                        step="0.01"
                        id={"inputItemAmount_" + this.props.itemIndex}
                        label="Amount"
                        required
                    />&nbsp;&nbsp; <b>INR ‚Çπ</b> &nbsp;&nbsp;
                </CInputGroup>


            </div>
        )
    }

}

export default ItemInputForm;
