import React, { Component } from 'react';
import { requestHeaderGETWithAuth, requestHeaderPOSTWithAuth } from 'src/Rest-API/Header';
import { getUserDataFromLocalStorage, logoutProcedureForLocalStorage } from 'src/Utils/AuthRelatedThings';
import { Logger, reloadPage, copyStringToClipboard } from '../../Utils/Utils';
import { Loading } from '../Statuses/Statuses';
import {
    CButton,
    CCard,
    CCardBody,
    CCardGroup,
    CCol,
    CContainer,
    CForm,
    CInput,
    CInputGroup,
    CInputGroupPrepend,
    CInputGroupText,
    CRow,  
    CTextarea} from '@coreui/react';
import CIcon from '@coreui/icons-react';
import StatusModal from '../Modals/StatusModal';
import FailureModal from '../Modals/FailureModal';
import Select from 'react-select';
import { expenseModesList, paymentBanksList, paymentInstrumentsList, yesAndNoList } from 'src/Utils/Enums';
import ItemInputForm from './ItemInputForm';
import { BACKEND_PRICE_TRACKER_SERVICE_BASE_URL } from 'src/Rest-API/EnvironmentSpecificThings';
import moment from 'moment';
import { MANDATORY_DENOTER_RED_STAR } from '../BasicRepetitiveComponents/FormRelated';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

//default
const FILE_NAME = "ExpenseCreate.js";

//default
const initialState = {
    isLoaded: false,
    loadingModalVisible: false,
    loadingModalState: 0,
    failureModalVisible: false,
    locationListData: [],
    locationListDataSelectTagSpecific: [],
    storeListData: [],
    storeListDataSelectTagSpecific: [],
    categoryListData: [],
    productListData: [],
    productListDataSelectTagSpecific: [],

    dynamicItemList: [],
    newExpenseData: {
        storeId: null,
        locationId: null,
        paymentInstrument: null,
        paymentBank: null,
        mode: null
    }
};


class ExpenseCreate extends Component {

    //default
    constructor(props) {
        super(props);
        this.state = JSON.parse(JSON.stringify(initialState));
    }

    //default
    componentDidMount() {
        document.title = "Expenses - Create";
        this.fetchLocationDataList();
    }

    //default
    resetStateToDefault() {
        this.setState(JSON.parse(JSON.stringify(initialState)));
    }

    //default
    reloadData() {
        this.resetStateToDefault();
        this.fetchLocationDataList();
    }

    fetchLocationDataList() {
        let API_URL = BACKEND_PRICE_TRACKER_SERVICE_BASE_URL + '/location/list?offset=0&from=admin-portal';
        const userData = getUserDataFromLocalStorage();

        fetch(API_URL, requestHeaderGETWithAuth(userData.authToken))
        .then(response => {
            if(response.status === 200)
                return response.json();
            if(response.status === 401) {
                this.setState({
                    loadingModalVisible: false,
                    loadingModalState: 0,
                    failureModalVisible: true
                });
                setTimeout(() => {
                    logoutProcedureForLocalStorage();
                    reloadPage();
                }, 500);
            } else {
                this.setState({
                    loadingModalVisible: false,
                    loadingModalState: 0,
                    failureModalVisible: true
                });
            }
            throw new Error("HTTP status is " + response.status);
        })
        .then(
            (result) => {
                Logger(FILE_NAME, "fetchLocationDataList", "FETCH_1", "result", result);
                if(result.code === 1001) {
                    //success
                    const locationListDataSelectTagSpecific = result.data.map((element) => {
                        return {
                            value: element.id,
                            label: element.name
                        };
                    });
                    this.setState({
                        locationListData: result.data,
                        locationListDataSelectTagSpecific
                    });
                    this.fetchStoreDataList();
                } else {
                    //something horrible happened
                    this.setState({
                        loadingModalVisible: false,
                        loadingModalState: 0,
                        failureModalVisible: true
                    });
                }
            },
            (error) => {
                Logger(FILE_NAME, "fetchLocationDataList", "FETCH_1", "error", error);
                this.setState({
                    loadingModalVisible: false,
                    loadingModalState: 0,
                    failureModalVisible: true
                });
            }
        );
    }

    fetchStoreDataList() {
        let API_URL = BACKEND_PRICE_TRACKER_SERVICE_BASE_URL + '/store/list?offset=0&from=admin-portal';
        const userData = getUserDataFromLocalStorage();

        fetch(API_URL, requestHeaderGETWithAuth(userData.authToken))
        .then(response => {
            if(response.status === 200)
                return response.json();
            if(response.status === 401) {
                this.setState({
                    loadingModalVisible: false,
                    loadingModalState: 0,
                    failureModalVisible: true
                });
                setTimeout(() => {
                    logoutProcedureForLocalStorage();
                    reloadPage();
                }, 500);
            } else {
                this.setState({
                    loadingModalVisible: false,
                    loadingModalState: 0,
                    failureModalVisible: true
                });
            }
            throw new Error("HTTP status is " + response.status);
        })
        .then(
            (result) => {
                Logger(FILE_NAME, "fetchStoreDataList", "FETCH_1", "result", result);
                if(result.code === 1001) {
                    //success
                    const storeListDataSelectTagSpecific = result.data.map((element) => {
                        return {
                            value: element.id,
                            label: element.name
                        };
                    });
                    this.setState({
                        storeListData: result.data,
                        storeListDataSelectTagSpecific
                    });
                    this.fetchCategoryDataList();
                } else {
                    //something horrible happened
                    this.setState({
                        loadingModalVisible: false,
                        loadingModalState: 0,
                        failureModalVisible: true
                    });
                }
            },
            (error) => {
                Logger(FILE_NAME, "fetchStoreDataList", "FETCH_1", "error", error);
                this.setState({
                    loadingModalVisible: false,
                    loadingModalState: 0,
                    failureModalVisible: true
                });
            }
        );
    }

    fetchCategoryDataList() {
        let API_URL = BACKEND_PRICE_TRACKER_SERVICE_BASE_URL + '/category/list?offset=0&from=admin-portal';
        const userData = getUserDataFromLocalStorage();

        fetch(API_URL, requestHeaderGETWithAuth(userData.authToken))
        .then(response => {
            if(response.status === 200)
                return response.json();
            if(response.status === 401) {
                this.setState({
                    loadingModalVisible: false,
                    loadingModalState: 0,
                    failureModalVisible: true
                });
                setTimeout(() => {
                    logoutProcedureForLocalStorage();
                    reloadPage();
                }, 500);
            } else {
                this.setState({
                    loadingModalVisible: false,
                    loadingModalState: 0,
                    failureModalVisible: true
                });
            }
            throw new Error("HTTP status is " + response.status);
        })
        .then(
            (result) => {
                Logger(FILE_NAME, "fetchCategoryDataList", "FETCH_1", "result", result);
                if(result.code === 1001) {
                    //success
                    this.setState({
                        categoryListData: result.data
                    });
                    this.fetchProductDataList();
                } else {
                    //something horrible happened
                    this.setState({
                        loadingModalVisible: false,
                        loadingModalState: 0,
                        failureModalVisible: true
                    });
                }
            },
            (error) => {
                Logger(FILE_NAME, "fetchCategoryDataList", "FETCH_1", "error", error);
                this.setState({
                    loadingModalVisible: false,
                    loadingModalState: 0,
                    failureModalVisible: true
                });
            }
        );
    }

    fetchProductDataList() {
        let API_URL = BACKEND_PRICE_TRACKER_SERVICE_BASE_URL + '/product/list?offset=0&from=admin-portal';
        const userData = getUserDataFromLocalStorage();

        fetch(API_URL, requestHeaderGETWithAuth(userData.authToken))
        .then(response => {
            if(response.status === 200)
                return response.json();
            if(response.status === 401) {
                this.setState({
                    loadingModalVisible: false,
                    loadingModalState: 0,
                    failureModalVisible: true
                });
                setTimeout(() => {
                    logoutProcedureForLocalStorage();
                    reloadPage();
                }, 500);
            } else {
                this.setState({
                    loadingModalVisible: false,
                    loadingModalState: 0,
                    failureModalVisible: true
                });
            }
            throw new Error("HTTP status is " + response.status);
        })
        .then(
            (result) => {
                Logger(FILE_NAME, "fetchProductDataList", "FETCH_1", "result", result);
                if(result.code === 1001) {
                    //success
                    const productListDataSelectTagSpecific = result.data.map((product) => {
                        return {
                            value: product.id,
                            label: product.name,
                            unitOfMeasurement: this.state.categoryListData.find(category => category.id === product.categoryId).unitOfMeasurement
                        };
                    });
                    this.setState({
                        isLoaded: true,
                        productListData: result.data,
                        productListDataSelectTagSpecific
                    });
                } else {
                    //something horrible happened
                    this.setState({
                        loadingModalVisible: false,
                        loadingModalState: 0,
                        failureModalVisible: true
                    });
                }
            },
            (error) => {
                Logger(FILE_NAME, "fetchProductDataList", "FETCH_1", "error", error);
                this.setState({
                    loadingModalVisible: false,
                    loadingModalState: 0,
                    failureModalVisible: true
                });
            }
        );
    }

    reloadSupportingData() {
        this.setState({
            locationListData: [],
            locationListDataSelectTagSpecific: [],
        });
        this.fetchLocationDataList();
    }

    addMoreItemInputForm = () => {
        const { dynamicItemList } = this.state;
        let tempDynamicItemList = dynamicItemList;
        tempDynamicItemList.push({
            productId: null
        });

        this.setState({ dynamicItemList: tempDynamicItemList });
    };

    onProductNameSelection = (choice, index) => {//index start from 0
        console.log('onProductNameSelection() : choice = ', choice, " itemIndex = ", index);
        const { dynamicItemList } = this.state;
        let tempDynamicItemList = dynamicItemList;
        tempDynamicItemList[index].productId = (choice === null) ? null : choice.value;
        this.setState({ dynamicItemList: tempDynamicItemList });
    }

    onItemDeletion = (itemIndex) => {
        return; // STILL WIP
        console.log('onItemDeletion() : itemIndex = ', itemIndex);
        this.setState({ dynamicItemList: [] });

        let tempDynamicItemList = [];
        for(let i = 0 ; i < this.state.dynamicItemList.length; i++) {
            if(i !== itemIndex)
                tempDynamicItemList.push(this.state.dynamicItemList[i]);
        }
        tempDynamicItemList.forEach(i => console.log(i));

        this.setState({ dynamicItemList: tempDynamicItemList });
    }

    submitCreateExpenseForm = (event) => {
        event.preventDefault();

        let reqData = {
            dateAndTimeOfPurchase: event.target.inputDateOfPurchase.value + " " + event.target.inputTimeOfPurchase.value,
            extraNotes: event.target.inputExtraNotes.value,
            tags: event.target.inputTags.value,
            payment: {
                totalAmount: event.target.inputTotalAmount.value
            }
        };

        const validationResult = this.doesStateContainAllRequiredExpenseParams();
        if(validationResult !== null) {
            alert(validationResult + " . ‚ÑπÔ∏è Form validation failed.");
            return;
        }
        reqData.mode = this.state.newExpenseData.mode;
        reqData.storeId = this.state.newExpenseData.storeId;
        reqData.locationId = this.state.newExpenseData.locationId;
        reqData.payment.paymentInstrument = this.state.newExpenseData.paymentInstrument;
        reqData.payment.bank = this.state.newExpenseData.paymentBank;

        const itemsDataList = [];
        this.state.dynamicItemList.forEach((element, index) => {
            itemsDataList.push({
                productId: element.productId,
                quantity: document.getElementById('inputItemQuantity_' + index).value,
                amount: document.getElementById('inputItemAmount_' + index).value,
            })
        });
        reqData.items = itemsDataList;

        Logger(FILE_NAME, "submitCreateExpenseForm", "", "reqData", reqData);
        this.setState({
            loadingModalVisible: true,
            loadingModalState: 0
        });
        this.callCreateExpenseApi(reqData);
    }

    doesStateContainAllRequiredExpenseParams() {
        if(this.state.newExpenseData.paymentInstrument === null)
            return "Payment-Instrument NOT selected";

        if(this.state.newExpenseData.paymentInstrument !== paymentInstrumentsList[4].value &&
            this.state.newExpenseData.paymentBank === null)
            return "Bank NOT selected, for NON-CASH payments";
        if(this.state.newExpenseData.mode === null)
            return "MODE NOT selected";
        if(this.state.newExpenseData.storeId === null)
            return "STORE not selected";
        if(this.state.newExpenseData.mode === "ONLINE" && this.state.newExpenseData.locationId === null)
            return "order-Location NOT selected for online order";
        if(this.state.dynamicItemList.length < 1)
            return "No item added in the expense";

        return null;
    }

    callCreateExpenseApi(newExpense) {
        let API_URL = BACKEND_PRICE_TRACKER_SERVICE_BASE_URL + '/expense/save?offset=0&from=admin-portal';
        const userData = getUserDataFromLocalStorage();
        fetch(API_URL, requestHeaderPOSTWithAuth(JSON.stringify(newExpense), userData.authToken))
        .then(response => {
            if(response.status === 200)
                return response.json();
            if(response.status === 401) {
                this.setState({
                    loadingModalVisible: false,
                    loadingModalState: 0,
                    failureModalVisible: true
                });
                setTimeout(() => {
                    logoutProcedureForLocalStorage();
                    reloadPage();
                }, 500);
            } else {
                this.setState({
                    loadingModalVisible: false,
                    loadingModalState: 0,
                    failureModalVisible: true
                });
            }
            throw new Error("HTTP status is " + response.status);
        })
        .then(
            (result) => {
                Logger(FILE_NAME, "callCreateExpenseApi", "FETCH_1", "result", result);
                if(result.code === 1001) {
                    //success
                    this.setState({
                        loadingModalState: 1,
                    });
                    setTimeout(() => {this.reloadData();}, 200);
                } else {
                    //something horrible happened
                    this.setState({
                        loadingModalVisible: false,
                        loadingModalState: 0,
                        failureModalVisible: true
                    });
                }
            },
            (error) => {
                Logger(FILE_NAME, "callCreateExpenseApi", "FETCH_1", "error", error);
                this.setState({
                    loadingModalVisible: false,
                    loadingModalState: 0,
                    failureModalVisible: true
                });
            }
        );
    }

    copyAndNotify(expenseTypeForEjipuraRentHouse) {
        copyStringToClipboard(expenseTypeForEjipuraRentHouse);
         toast("‚úÖ üìã COPIED " + expenseTypeForEjipuraRentHouse + " to clipboard !");
    }

    //default
    render() {
        //default
        Logger(FILE_NAME, "render", "", "this.state", this.state);

        return (            
                this.state.isLoaded ? 
                <div>
                    <h1>Create a new Expense 
                        <br />
                        <div style={{
                            borderStyle:"solid",
                            borderColor:"#ff9100",
                            padding: "10px 10px 10px 10px",
                            margin: "0px 0px 10px 0px"
                        }}>
                            <CButton color="success" className="px-4" onClick={() => this.reloadData()}>Reload Data</CButton>
                            &nbsp;&nbsp;&nbsp;&nbsp;&nbsp; 
                            <CButton color="success" className="px-4" onClick={() => this.reloadSupportingData()}>Reload Supporting Data(background)</CButton>
                            &nbsp;&nbsp;&nbsp;&nbsp;&nbsp; 
                            <CButton color="danger" className="px-4" onClick={() => { window.open('./#/price_tracker/expense/view', '_self'); }}> üîó View expenses</CButton>
                        </div>
                    </h1>
                    <br />

                    {
                    /* Create new Expense */
                    <CContainer>
                        <CRow className="justify-content-center">
                            <CCol md="8">
                                <CCardGroup>
                                <CCard className="p-4">
                                    <CCardBody>
                                    <CForm onSubmit={this.submitCreateExpenseForm}>

                                        <div style={{
                                            borderStyle:"solid",
                                            borderColor:"blue",
                                            padding: "10px 10px 10px 10px",
                                            margin: "0px 0px 10px 0px"
                                        }}>
                                            <h4><CIcon size='lg' name="cilCalendar" />&nbsp;&nbsp; <u>Date and time of Purchase :</u> <MANDATORY_DENOTER_RED_STAR /></h4>
                                            <p>‚õîÔ∏è ‚ÑπÔ∏è : <i>date format on UI is : DD/MM/YYYY</i></p>
                                            <CInputGroup className="mb-3">
                                                <CInputGroupPrepend>
                                                    <CInputGroupText>
                                                    <CIcon name="cilCalendar" /> 
                                                    </CInputGroupText>
                                                </CInputGroupPrepend>
                                                <CInput
                                                    placeholder="Date Of Purchase" 
                                                    autoComplete="on"
                                                    type="date"
                                                    defaultValue={new moment().format('YYYY-MM-DD')} //2020-03-08
                                                    step="any"
                                                    id="inputDateOfPurchase"
                                                    label="Date Of Purchase"
                                                    required
                                                />
                                                <CInput
                                                    placeholder="Time Of Purchase" 
                                                    autoComplete="on"
                                                    type="time"
                                                    step="any"
                                                    id="inputTimeOfPurchase"
                                                    label="Time Of Purchase"
                                                    required
                                                />
                                            </CInputGroup>
                                        </div>

                                        <div style={{
                                            borderStyle:"solid",
                                            borderColor:"green",
                                            padding: "10px 10px 10px 10px",
                                            margin: "0px 0px 10px 0px"
                                        }}>
                                            <h4><CIcon size='lg' name="cilDollar" /><CIcon size='lg' name="cilEuro" />&nbsp;&nbsp; <u>Payment related data :</u></h4>

                                            <h5><CIcon size='lg' name="cibPaypal" />&nbsp;&nbsp;Payment instrument : <MANDATORY_DENOTER_RED_STAR /></h5>
                                            <Select 
                                                onChange={(choice) => {
                                                    const newExpenseData = this.state.newExpenseData;
                                                    newExpenseData.paymentInstrument = (choice === null) ? null : choice.value;
                                                    this.setState({
                                                        newExpenseData
                                                    });
                                                }}
                                                options={paymentInstrumentsList}
                                                isClearable={true}
                                            />
                                            <br />

                                            <h5><CIcon size='lg' name="cilBank" />&nbsp;&nbsp;Bank : <MANDATORY_DENOTER_RED_STAR /></h5>
                                            <Select 
                                                onChange={(choice) => {
                                                    const newExpenseData = this.state.newExpenseData;
                                                    newExpenseData.paymentBank = (choice === null) ? null : choice.value;
                                                    this.setState({
                                                        newExpenseData
                                                    });
                                                }}
                                                options={paymentBanksList}
                                                isClearable={true}
                                            />
                                            <br />

                                            <h5><CIcon size='lg' name="cilMoney" />&nbsp;&nbsp;Total amount paid : <MANDATORY_DENOTER_RED_STAR /></h5>
                                            <CInputGroup className="mb-3">
                                                
                                                <CInput
                                                    placeholder="Total amount" 
                                                    autoComplete="on"
                                                    type="number"
                                                    step="0.01"
                                                    id="inputTotalAmount"
                                                    label="Total amount"
                                                    required
                                                />
                                            </CInputGroup>
                                        </div>

                                        <h4><CIcon size='lg' name="cilLocationPin" />&nbsp;&nbsp; Expense mode : <MANDATORY_DENOTER_RED_STAR /></h4>
                                        <Select 
                                            onChange={(choice) => {
                                                const newExpenseData = this.state.newExpenseData;
                                                newExpenseData.mode = (choice === null) ? null : choice.value;
                                                this.setState({
                                                    newExpenseData
                                                });
                                            }}
                                            options={expenseModesList}
                                            isClearable={true}
                                        />
                                        <br />

                                        <h4><CIcon size='lg' name="cilLocationPin" />&nbsp;&nbsp; Store : <MANDATORY_DENOTER_RED_STAR /></h4>
                                        <Select 
                                            onChange={(choice) => {
                                                const newExpenseData = this.state.newExpenseData;
                                                newExpenseData.storeId = (choice === null) ? null : choice.value;
                                                this.setState({
                                                    newExpenseData
                                                });
                                            }}
                                            options={this.state.storeListDataSelectTagSpecific}
                                            isClearable={true}
                                        />
                                        <br />

                                        <h4>
                                            <CIcon size='lg' name="cilLocationPin" />&nbsp;&nbsp; Location :
                                            <h6>{(this.state.newExpenseData.mode === 'STORE_BOUGHT') ? "‚õîÔ∏è ‚ÑπÔ∏è Not reqd. for store-bought items" : "" } </h6>
                                        </h4>
                                        <Select 
                                            onChange={(choice) => {
                                                const newExpenseData = this.state.newExpenseData;
                                                newExpenseData.locationId = (choice === null) ? null : choice.value;
                                                this.setState({
                                                    newExpenseData
                                                });
                                            }}
                                            options={this.state.locationListDataSelectTagSpecific}
                                            isClearable={true}
                                        />
                                        <br />

                                        {/* ADDED for managing+tracking EjipuraRentHouse expense */}
                                        {
                                            ['personal_arsh', 'for_ejipura_house']
                                            .map((item, index) => {
                                                return <span>
                                                    <CButton color="danger" className="px-0" onClick={() => this.copyAndNotify(item)}>&nbsp;üìã {item}&nbsp;</CButton>
                                                    &nbsp;&nbsp;&nbsp;&nbsp;&nbsp; 
                                                    </span>
                                            })
                                        }

                                        <h4>
                                            <CIcon size='lg' name="cilPencil" />&nbsp;&nbsp; Notes : 
                                        </h4>
                                        <CInputGroup className="mb-3">
                                            <CInputGroupPrepend>
                                                <CInputGroupText>
                                                    <CIcon name="cilPencil" />
                                                </CInputGroupText>
                                            </CInputGroupPrepend>
                                            <CTextarea  
                                                placeholder='Some extra notes üìù (not-mandatory)' 
                                                id="inputExtraNotes" 
                                                rows="3"
                                            />
                                        </CInputGroup>

                                        <h4>
                                            <CIcon size='lg' name="cilTags" />&nbsp;&nbsp; Tags :
                                        </h4>
                                        <CInput
                                            placeholder="tags üè∑Ô∏è üîñ (not-mandatory)" 
                                            autoComplete="on"
                                            step="any"
                                            id="inputTags"
                                            label="expense-tags"
                                            required={false}
                                        />
                                        <br />

                                        {/* dynamic items input-list */}
                                        <div>
                                        {
                                        this.state.dynamicItemList.length >= 1 &&
                                        this.state.dynamicItemList.map((item, index) => {
                                            return (
                                            <ItemInputForm 
                                                key={"itemInputFormComponent_" + index}
                                                data={item}
                                                {...this.props}
                                                itemIndex={index} // starts from 0
                                                productListDataSelectTagSpecific={this.state.productListDataSelectTagSpecific}
                                                onProductNameSelection={this.onProductNameSelection}
                                                onItemDeletion={this.onItemDeletion}
                                            />
                                            );
                                        })}
                                        </div>
                                        <CButton color="info" className="px-4" onClick={this.addMoreItemInputForm}>Add item ‚ûï </CButton>
                                        &nbsp;&nbsp;
                                        <span><b>Current ITEM Count (Œ£) = {this.state.dynamicItemList.length}</b></span>
                                        <br /><br />

                                        <CRow>
                                            <CCol xs="6">
                                                <CButton color="success" className="px-4" type='submit'>SAVE</CButton>
                                            </CCol>
                                            <CCol xs="6" className="text-right">
                                                <CButton color="danger" className="px-0" onClick={() => this.reloadData()}>&nbsp;Reset values&nbsp;</CButton>
                                            </CCol>
                                        </CRow>
                                    </CForm>
                                    </CCardBody>
                                </CCard>
                                </CCardGroup>
                            </CCol>
                        </CRow>
                        <ToastContainer
                            position="bottom-center"
                            autoClose={500}
                            hideProgressBar={false}
                            newestOnTop={false}
                            closeOnClick
                            rtl={false}
                            pauseOnFocusLoss
                            draggable
                            pauseOnHover
                            theme="light"
                            />
                    </CContainer>   
                    }

                <br /><br />
                    <StatusModal visible={this.state.loadingModalVisible} loadingModalState={this.state.loadingModalState} />
                    <FailureModal visible={this.state.failureModalVisible} />
                </div>
                : <Loading />
        );
    }
}

export default ExpenseCreate;

